<script>
  export default {
    data() {
      return {
        user: {
          username:"",
          id: "",
          email: ""
        },
        session: {
          active: false,
          id: null,
          username: '',
          email: ''
        }
      }
    },
    mounted () {
      if (!localStorage.session) {
        localStorage.session = JSON.stringify({
          ...this.session
        })} else {
          this.session = JSON.parse(localStorage.session)
      }
    },
    watch: {
      '$route' (to, from) {
        if(from.name === 'login' && to.name === 'home') {
          this.session = JSON.parse(localStorage.session)
        }
      }
    },
    methods: {
      unregisterUser: function() {
        const newSession = {
            active: false,
            id: null,
            username: '',
            email: ''
        }
        localStorage.session = JSON.stringify(newSession)
        this.session = newSession
        router.push('/')
      },
    }
  }
</script>

